import React from 'react';
import "./modal.css";

class Modal extends React.Component {
    render() {
        var feedbackAvailable = this.props.data.feedback !== undefined && this.props.data.feedback !== null;

        return(
            <div className="modal">
                <div className="overlay" onClick={this.props.onClick}></div>
                <div className="modal-content">
                    <h1 className="title">{this.props.data.title}</h1>
                    <p className="body">{this.props.data.body}</p>
                    { feedbackAvailable && <p className="feedback">{this.props.data.feedback}</p> }
                    <button className=".btn-modal btn-modal-close" onClick={this.props.onClick}>X</button>
                </div>
            </div>
        )
    }
}

export default Modal;